(function() {
  'use strict';

  angular
    .module('newsmaker', ['ngAnimate', 'ngCookies', 'ngTouch', 'ngSanitize', 'ngMessages', 'ngAria', 'ui.router', 'ui.bootstrap', 'toastr']);

})();

(function() {
  'use strict';

  angular
    .module('newsmaker')
    .directive('acmeNavbar', acmeNavbar);

  /** @ngInject */
  function acmeNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {
          creationDate: '='
      },
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController() {
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('newsmaker')
    .controller('ModalInstanceController', ModalInstanceController);

  function ModalInstanceController($uibModalInstance, bbCode, title) {
    var vm = this;

    vm.bbCode = bbCode;
    vm.title = title;
    vm.ok = ok;
    vm.cancel = cancel;

    function ok() {
      $uibModalInstance.close();
    }

    function cancel() {
      $uibModalInstance.dismiss('cancel');
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('newsmaker')
    .factory('MainService', mainService);

  function mainService($http, $log) {
    var template = undefined;
    var templateUrl = './assets/template';
    var templateConstants = {
      headline1: '###HEADLINE1###',
      headline2: '###HEADLINE2###',
      img: '###IMG###',
      source: '###SOURCE###',
      text: '###TEXT###',
      comment: '###COMMENT###'
    };

    //////////////////////////////////////////////////////////////////////////////////////////////////
    var service = {
      createCode: createCode,
      getTemplate: getTemplate,
      initTemplate: initTemplate
    };

    return service;
    //////////////////////////////////////////////////////////////////////////////////////////////////

    function createCode(data) {
      var bbCode = template;
      angular.forEach(templateConstants, function(value, key) {
        bbCode = bbCode.replace(value, data[key]);
      });
      return bbCode;
    }

    function getTemplate() {
      return template;
    }

    function initTemplate() {
      $http.get(templateUrl).success(function(data) {
        template = data.replace('###TEMPLATE###\n', '');
        $log.info('MainService.initTemplate: Successfully initilialized template');
      }).error(function(error) {
        $log.error('MainService.initTemplate: Error initializing template. ' + error);
      });
    }


  }
})();

(function() {
  'use strict';

  angular
    .module('newsmaker')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController(MainService, $uibModal) {
    var vm = this;

    vm.newsDto = {};
    vm.bbCode = '';
    vm.clear = clear;
    vm.createCode = createCode;
    vm.initTemplate = initTemplate;

    function clear() {
      vm.newsDto = {};
    }

    function createCode() {
      vm.bbCode = MainService.createCode(vm.newsDto);

      $uibModal.open({
        animation: true,
        templateUrl: 'bbCodeModal.html',
        controller: 'ModalInstanceController',
        controllerAs: 'modali',
        size: 'lg',
        resolve: {
          bbCode: function () {
            return vm.bbCode;
          },
          title: function () {
            return 'News: BB-Code!';
          }
        }
      });
    }

    function initTemplate() {
      MainService.initTemplate();
    }
    vm.initTemplate();
  }
})();

(function() {
  'use strict';

  angular
    .module('newsmaker')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log) {

    $log.debug('runBlock end');
  }

})();

(function() {
  'use strict';

  angular
    .module('newsmaker')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('home', {
        url: '/',
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'main'
      });

    $urlRouterProvider.otherwise('/');
  }

})();

/* global malarkey:false, moment:false */
(function() {
  'use strict';

  angular
    .module('newsmaker')
    .constant('malarkey', malarkey)
    .constant('moment', moment);

})();

(function() {
  'use strict';

  angular
    .module('newsmaker')
    .config(config);

  /** @ngInject */
  function config($logProvider, toastrConfig) {
    // Enable log
    $logProvider.debugEnabled(true);

    // Set options third-party lib
    toastrConfig.allowHtml = true;
    toastrConfig.timeOut = 3000;
    toastrConfig.positionClass = 'toast-top-right';
    toastrConfig.preventDuplicates = true;
    toastrConfig.progressBar = true;
  }

})();

